"use client";
import { useRouter } from "next/navigation";
import { Typography, Box, Button, CircularProgress } from "@mui/material";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import SaveIcon from "@mui/icons-material/Save";
import CustomForm from "@/app/components/CustomForm";
import { getApi } from "@/utils/getApiMethod";

const AddVehicle = () => {
  const router = useRouter();
  const [formSchema, setFormSchema] = useState([]);
  const [form, setForm] = useState({});
  const [loading, setLoading] = useState(true);

  // === Fetch vehicle fields dynamically ===
  useEffect(() => {
    const fetchVehicleFields = async () => {
      setLoading(true);
      try {
        const result = await getApi("/fieldindex01/vehicleform?entity_name=Vehicle");
        console.log("result", result);
        if (result?.structure) {
          const structure = result.structure;
          console.log("structure", structure);
          setFormSchema(structure);

          // initialize form state based on fields
          const initialForm = structure.reduce((acc, tab) => {
            tab.sections.forEach((section) => {
              section.fields.forEach((field) => {
                acc[field.key] =
                  field.type === "multiselect"
                    ? []
                    : field.type === "switch"
                      ? false
                      : "";
              });
            });
            return acc;
          }, {});

          setForm(initialForm);
        } else {
          console.error("Unexpected response format:", result);
        }
      } catch (error) {
        console.error("Error fetching vehicle fields:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchVehicleFields();
  }, []);

  // === Handlers ===
  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSave = () => {
    console.log("Form Data Submitted:", form);
    // TODO: Add your POST API here
  };

  const handleBack = () => {
    router.back();
  };

  // === Loading State ===
  if (loading) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "80vh",
        }}
      >
        <CircularProgress />
      </Box>
    );
  }

  // === Render Form ===
  return (
    <motion.div
      initial={{ opacity: 0, scale: 0.9, x: -20 }}
      animate={{ opacity: 1, scale: 1, x: 0 }}
      exit={{ opacity: 0, scale: 0.9, x: 20 }}
      transition={{ duration: 1, ease: [0.4, 0, 0.2, 1] }}
      style={{
        height: "95vh",
        overflowY: "auto",
        display: "flex",
        flexDirection: "column",
      }}
    >
      {/* Header */}
      <Box
        sx={{
          flexShrink: 0,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Box>
          <Typography variant="h6" sx={{ fontWeight: 700 }}>
            Add Vehicle
          </Typography>
          <Typography variant="body2" sx={{ color: "#666" }}>
            Fill in the details below to add a new vehicle.
          </Typography>
        </Box>

        <Box>
          <Button
            className="btn-primary"
            sx={{ textTransform: "none", marginRight: 1 }}
            startIcon={<SaveIcon />}
            onClick={handleSave}
          >
            Save
          </Button>
          <Button
            className="btn-secondary"
            sx={{ textTransform: "none" }}
            startIcon={<ArrowBackIcon />}
            onClick={handleBack}
          >
            Back
          </Button>
        </Box>
      </Box>

      {/* Dynamic Form */}
      <CustomForm
        formSchema={formSchema}
        formData={form}
        onChange={handleChange}
      />
    </motion.div>
  );
};

export default AddVehicle;
