"use client";
import { TextField } from "@mui/material";

const CustomTextarea = ({
    label,
    name,
    value,
    onChange,
    required,
    disabled,
    rows = 4,
}) => (
    <TextField
        size="small"
        label={label}
        name={name}
        value={value || ""}
        onChange={onChange}
        fullWidth
        margin="dense"
        required={required}
        disabled={disabled}
        multiline
        rows={rows}
        sx={{
            "& .MuiOutlinedInput-root": {
                borderRadius: 2,
                background: "#fff",
                "& fieldset": {
                    borderColor: "#e5e7eb",
                },
                "&:hover fieldset": {
                    borderColor: "#7e5bef",
                },
                "&.Mui-focused fieldset": {
                    borderWidth: "2px",
                    borderColor: "#7e5bef",
                },
            },
            "& .MuiInputLabel-root.Mui-focused": {
                color: "#7e5bef",
            },
        }}
    />
);

export default CustomTextarea;
